<template>
<!-- 自定义的动画样式要用transition -->
  <transition
  class="animate__animated"
    name="animate__animated animate__bounce"
    enter-active-class="animate__backInDown"
    leave-active-class="animate__backOutDown"
  >
    <div class="admin">
      <div class="admin_wrap" v-if="this.ruleForm.show">
          <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="管理员账号" prop="adminName">
                <el-input type="text" v-model="ruleForm.adminName" ></el-input>
            </el-form-item>
            <el-form-item label="管理员密码" prop="adminPass">
                <el-input type="password" v-model="ruleForm.adminPass" ></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
            </el-form>
      </div>
      <div class="loginIn" v-else>
          <p>登录成功</p>
          <el-button type="primary" @click="destroy">退出登录</el-button>
          <el-button type="primary" @click="intoThePage">进入管理页面</el-button>
      </div>
  </div>
  </transition>
</template>

<script>
export default {
    name:'adminbe',
    data() {
      var validateadminName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入管理员账号'));
        } else {
          callback();
        }
      };
      var validateadminPass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入管理员密码'));
        } else {
          callback();
        }
      };
      return {
        ruleForm: {
          adminName: '',
          adminPass: '',
          show:true,
        },
        rules: {
          adminName: [
            { validator: validateadminName, trigger: 'blur' }
          ],
          adminPass: [
            { validator: validateadminPass, trigger: 'blur' }
          ],
          
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate(async(valid) => {
          if (valid) {
            //管理员登录
            let {data} = await this.$axios.get('/node/admin/loginIn',{})
            // console.log(data);
            if(this.ruleForm.adminName == data.adminName && this.ruleForm.adminPass == data.adminPass){
                this.ruleForm.show = false
                this.$message.success('登录成功')
                //存入vuex仓库
                this.$store.commit('adminStore',data)
            }else{
                this.$message.error('账号或密码输入错误')
            }
          } else {
            this.$message.error('请输入完整')
          }

        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      destroy(){
          this.$store.commit('adminStore','')
          this.ruleForm.show = true
          this.$message.success('退出登录成功')
          location.reload()
      },
      intoThePage(){
        // console.log(this.$router.path);
        this.$router.push('/admin')
        // console.log('jinru');
        setTimeout(() => {
          //子组件无法直接修改父组件传的值，因此利用调用传来的函数修改
          this.$emit('changeShow')
        }, 2000);
      },
    }
}
</script>

<style lang="less">
.admin{
    position: fixed;
    z-index: 3;
    top: 55%;
    right: 20px;
    // background-color: pink;
    // padding-right: 20px;
    // padding-top: 20px;
    box-shadow: 1px 2px 6px 2px #ccc;
    // background: white;
    background: white;
    // filter: blur(0px) opacity(1);
    // backdrop-filter: blur(16px);
    border: 1px solid #ccc;
    width: 350px;
    height: 200px;
    .admin_wrap{
        width: 330px;
        height: 190px;
        margin: 10px 20px 0 0;
        // background: white;
        // filter: blur(0px) opacity(1);
        // backdrop-filter: blur(16px);
    }
}
</style>